import React, { useEffect, useState } from 'react'
import './projectdet.css'
import AllProjects from './AllProjectdata'

import  {Rotate,Bounce,Zoom,Roll,Flip}  from 'react-reveal';
function ProjectDetailes() {

    useEffect(() => {
        document.getElementsByClassName("abou1")[0].style.display = "none";
        document.getElementsByClassName("abou2")[0].style.display = "none";
        document.getElementsByClassName("abou3")[0].style.display = "none";
        document.getElementsByClassName("abou4")[0].style.display = "none";
        // document.getElementsByClassName("aboutsection")[0].style.display = "none";
        // document.getElementsByClassName("Projectss")[0].style.display = "none";
        // document.getElementsByClassName("experience")[0].style.display = "none";
        // document.getElementsByClassName("homemaindis")[0].style.display = "none";
        // document.getElementsByClassName("rabta")[0].style.display = "none";
      }, []);


      const [items, Setitem] = useState(AllProjects)

  return (
    <div className='moreproject'>
        <div style={{marginTop:"10%",padding:"0% 0% 5% 15%"}} >
        
            <div className="container">
                {
                    items.map((value)=>{
                        return (
                            
                        <div className="row">
                            {/* <Roll left> */}
                            <div className="col-sm-6">
                                <h3 style={{marginBottom:"8%"}}><b>{value.title}</b></h3>
                                <h5 style={{fontSize:'1rem', fontWeight:"400",marginBottom:"3%"}}>{value.desc}</h5>
                                <span style={{fontSize:"1rem"}}>Tools used  <span style={{marginLeft:"5%"}}>: {value.tool}</span> <br /> </span> <br />
                                {/* <span style={{fontSize:"1rem"}} className='bottomcat'>Category  <span style={{marginLeft:"6%"}}>: {value.category}</span></span> <br /> <br /> */}
                                <span style={{fontSize:"1rem"}}>Year  <span style={{marginLeft:"12.3%"}}>: {value.year}</span> </span> <br /> <br /> <br />
                                
                                <a href={value.githublink} target='_blank' className='hov' ><i className="fa-brands fa-github fa-xl ico" style={{color: "#000000;",marginLeft:"-5%",marginBottom:"10%"}}></i></a>
                                
                            </div>
                            {/* </Roll> */}
                            <Zoom>
                            <div className="col-sm-6">
                                <img src={value.image} alt="" style={{width:"60%", height:"80%", marginLeft:"5%"}} id='pdimg' />
                            </div>
                            </Zoom>
                            <hr style={{width:"83%"}}/>
                        </div>
                        )
                    })
                }
            </div>
        </div>
    </div>
  )
}

export default ProjectDetailes