import React from 'react'
// import about from '../../Assets/abouttwo.jpg'
import about from '../../Assets/aboutmeiqo.jpg'
import './About.css'
import  {Rotate,Bounce,Zoom,Roll}  from 'react-reveal';
function About() {
  return (
    <section id='aboutsection' className='aboutsection'>
      <div className="container aboutmain">
        <div className="row ">
            <Zoom>
          <div className="col-sm-6">
              <img id='aboutimg' src={about} alt="" />
          </div>
            </Zoom>
          <div className="col-sm-6">
            <h4 style={{color:"blue"}} className='abo'><b>About Me  <span style={{ color: "black" }}>:</span></b></h4>
            <h2 className='aboutcon'><b>A data enthusiast and learner in the realms of data science and data analysis. </b></h2>
             <p id='aboutmecontent'><small>Hi I am
               <b> Iqra Azhar</b> As an accomplished Data Scientist and Data Analyst, my portfolio speaks volumes about my ability to navigate the intricate world of data. With a diverse range of projects, I showcase my expertise in statistical modeling, machine learning, and data visualization. From predictive analytics to interactive dashboards, I demonstrate how I unearth valuable insights to drive informed decision-making. My fusion of technical proficiency and effective communication ensures that my findings resonate with stakeholders at all levels, fueling data-driven strategies that lead to tangible business outcomes. With an unwavering passion for unleashing the power of data, I embrace every challenge as an opportunity to showcase excellence, innovation, and a relentless pursuit of success in this fast-paced, data-centriclandscape.</small></p>   
          </div>
        </div>
      </div>
    </section>
  )
}

export default About