import React, { useEffect, useState } from 'react'
import AllData  from './AllData'
import './Galary.css'

import  {Rotate,Bounce,Zoom,Roll,Flip}  from 'react-reveal';
function GalaryPoject() {


   
    const [items, Setitem] = useState(AllData)

    const allitem = ()=>{
        const finalData = AllData.filter((value)=>{
            return value;
        })
        Setitem(finalData)
    }

    const onlypython  = (catogaryitem)=>{
        const finalData = AllData.filter((value)=>{
            return value.category === catogaryitem
        })
        Setitem(finalData)
    }
    const onlycss  = (catogaryitem)=>{
        const finalData = AllData.filter((value)=>{
            return value.category === catogaryitem
        })
        Setitem(finalData)
    }
    const onlyhtml  = (catogaryitem)=>{
        const finalData = AllData.filter((value)=>{
            return value.category === catogaryitem
        })
        Setitem(finalData)
    }
    const onlyreact  = (catogaryitem)=>{
        const finalData = AllData.filter((value)=>{
            return value.category === catogaryitem
        })
        Setitem(finalData)
    }
    // const onlyaspdotnet  = (catogaryitem)=>{
    //     const finalData = AllData.filter((value)=>{
    //         return value.category === catogaryitem
    //     })
    //     Setitem(finalData)
    // }


  return (
    <div>
        <div className="container-fluid " style={{marginTop:"10%"}}>
            
            <div className="row bg-white btnback ">
                <div className="col-sm-1 ">
                    <button className='btn' onClick={allitem}><b><span className='innderbtn'>All</span></b></button>
                </div>
                <div className="col-sm-1">
                    <button className='btn' onClick={()=>onlypython('python')}> <b><span className='innderbtn'>PowerBi</span></b> </button>
                </div>
                <div className="col-sm-1">
                    <button className='btn' onClick={()=>onlycss('css')}> <b><span className='innderbtn'>MsExcel</span></b></button>
                </div>
                <div className="col-sm-1">
                    <button className='btn' onClick={()=>onlyhtml('html')}> <b><span className='innderbtn'>Mysql</span></b> </button>
                </div>
                <div className="col-sm-1">
                    <button className='btn' onClick={()=>onlyreact('react')}> <b><span className='innderbtn'>Python</span></b> </button>
                </div>
                {/* <div className="col-sm-1">
                    <button className='btn' onClick={()=>onlyaspdotnet('aspdotnet')}> <b><span className='innderbtn'>AspDotNet</span></b> </button>
                </div> */}
            </div>
                    {/* // <img src={value.image} className='img-fluid' alt="" /> */}
   
            <div className="container-fluid mt-4">
                <div className="row">
                {
                    items.map((value)=>{
                        return(
                            <div className="col-sm-3">
                            <div class="card mb-3">
                               <div class="content">
                               <Zoom>
                                    {/* <a href={value.linkadd} target="_blank" style={{display:"flex"}}> */}
                                    <a href={value.linkadd}  style={{display:"flex"}}>
                                    <div class="content-overlay"></div>
                                    <img className='img-fluid content-image' src={value.image} alt=""/>
                                    <div class="content-details fadeIn-top">
                                        <h3 style={{fontSize:"1.3rem"}}>{value.title}</h3>
                                        <p>{value.desc}</p>
                                        <a href={value.githublink}><i className="fa-brands fa-github fa-xl projectgit"  style={{color:"white"}}></i></a>
                                    </div>
                                    </a>
                               </Zoom>
                                </div>
                            </div>
                            </div>
                        )
                    })
                }

                </div>
            </div>
        </div>
    </div>
  )
}

export default GalaryPoject